  async alertWithOkButton(message) {
    const alert = await this.alertController.create({
      header: '',
      message: message,
      buttons: ['']
    });
    await alert.present();
  }

  async toastWithoutCallback(message) {
    const toast = await this.toastController.create({
      message: message,
      showCloseButton: false,
      position: 'middle',
      duration: 1000
    });
    await toast.present();
  }

  async toastWithCallbackPop(message, event) {
    const toast = await this.toastController.create({
      message: message,
      showCloseButton: false,
      position: 'middle',
      duration: 1000
    });
    await toast.present();
    await this.router.navigate(['../'])
  }

  async toastWithCallbackPopToRoot(message, event) {
    const toast = await this.toastController.create({
      message: message,
      showCloseButton: false,
      position: 'middle',
      duration: 1000
    });
    await toast.present();
    await this.router.navigate(['/tabs/personal'])
  }

  /**
   * 
   * @param id 
   */
  oooTab(id) {
    $("#" + id).addClass("current").siblings().removeClass("current").parent().siblings().hide().siblings("." + id).show();
  }

  /**
   * 
   * @param id id
   */
  oooTab2(id) {
    if (id == 0) {
      $('.tabNav').find('li')[0].children[0].setAttribute('class', 'cur')
      $('.tabCon')[0].setAttribute('style', 'display: block;')
      $('.tabNav').find('li')[1].children[0].setAttribute('class', '')
      $('.tabCon')[1].setAttribute('style', 'display: none;')
    } else if (id == 1) {
      $('.tabNav').find('li')[0].children[0].setAttribute('class', '')
      $('.tabCon')[0].setAttribute('style', 'display: none;')
      $('.tabNav').find('li')[1].children[0].setAttribute('class', 'cur')
      $('.tabCon')[1].setAttribute('style', 'display: block;')
    }
  }

