  /**
   * 
   */

  checkUser(): boolean {
    var userId = this.storage.get("user_id")
    // 
    if (!userId) {
      this.router.navigate(['/login'])
      return false
    } else {
      this.getUserApi(userId).then((data: any) => {
        if (data.code == 0) {
          var userInfo = data.userInfo
          if (userInfo.activate == false) {
            var message = ''
            this.toastWithCallbackPop(message, this)
            return false
          }
        }
      })
    }
    return true
  }

